* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}
body {
  margin: 100px;
  background-color: rgb(255, 255, 255);
}
.to-do{
  background-color: rgb(236, 236, 236);
  text-align: center;
  width: 100%;
  min-height: 160px;
  border-radius: 7px;
  padding: 10px 10px;

}
input{
  width:80% ;
  font-size: 17px;
  height: 40px;
  outline: none;
  border-radius:4px ;
  border: 1px solid black;
  box-shadow: 1px  1px 4px black;
}
button{
  border: none;
  box-shadow: 1px  1px 4px black;
  padding: 0px 0px;
  height: 43px;
  margin: 3px;
  border-radius: 4px;
}
ol{
  padding: 0;
}
li{
  display: flex;
  align-items: center;
  margin: 4px;
  border: 1px black solid;
  color: rgb(0, 0, 0);
  background-color: rgb(252, 252, 252);
  border-radius: 4px;
  box-shadow: 1px  1px 4px black;
}
.text{
  flex: 1;
  
}
.delete-but , .move-but{
  padding: 5px 8px;
  font-size: 10px;
}
.delete-but{
  background-color: brown;
}
.move-but{
  background-color: aqua;
}
.add-button{
  background-color: rgb(95, 196, 27);
}
span{
  font-size: small;
}
h1{
  transform: rotate(0deg);
  color: rgb(0, 0, 0);
  border-radius: 30px;
}
header{
  color: antiquewhite;
  background-color: rgb(78, 110, 158);
  width: 100%;
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  height: 30px;
}
.switch-theam{
  background-color: aqua;
  width: 50px;
  height: fit-content;
  border: none;
  border-radius: 3px;

}